.svg {
  display: block;
  max-width: 100%;
  
  .svg-circle-bg {
    fill: none;
  }

  .svg-circle {
    fill: none;
    transition: stroke-dashoffset 450ms ease-in-out;
  }

  .svg-circle-text {
    font-size: 14px;
    text-anchor: middle;
    fill: #45536C;
    font-weight: bold;
  }
}
